import React from 'react'
import axios from 'axios'
import TableTemp from './components/table.jsx'
import Forecast from './components/forecast.jsx'
import CurrentWeather from './components/currentWeather.jsx'
import Tab from './components/tab.jsx'


function CustomTab(props){
  const currentStyle = {
    textAlign: 'left',
    paddingLeft: '1.5em'
  }
  if(props.currentWeather && props.forecastWeather){
    return (
      <Tab currentWeather={props.currentWeather} forecastWeather={props.forecastWeather} customStyle={currentStyle}/>
    );
  }
  return null
}

class WeatherData extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      currentWeather: null,
      forecastWeather: null
    }
    this.weatherTimer = null
  }

  getWeatherData(){
    //console.log(this)
    
    clearInterval(this.weatherTimer)
    const cityName = this.state.currentWeather.name
    let finalCityName
    cityName == "Athens" ? finalCityName = cityName.concat(',GR') : finalCityName = cityName

    let currentWeather = null
    let forecastWeather = null

    this.weatherTimer = setInterval(function() {
      axios.get(`https://api.openweathermap.org/data/2.5/weather?APPID=dfbbc23cb4274f50546c28374b6ad2ee&units=metric`+ '&q='+ finalCityName)
      .then(res => {
        currentWeather = res.data
      })
      axios.get(`https://api.openweathermap.org/data/2.5/forecast?APPID=dfbbc23cb4274f50546c28374b6ad2ee&units=metric` + '&q='+ finalCityName)
      .then(res => {
        forecastWeather = res.data.list
      })
      //add timeout cause it take a little time to add the data to the variables
      setTimeout(function(){
        this.setState({ currentWeather,forecastWeather })
      }.bind(this),1000)

    }.bind(this),600000)
  }

  componentDidMount() {
    let currentWeather
    let forecastWeather
      //console.log("den einai orismena")
      //gurnaei 4 result gia athens
      //axios.get(`https://openweathermap.org/data/2.5/find?callback=jQuery19106827186598114341_1536529677992&q=athens&type=like&sort=population&cnt=30&appid=b6907d289e10d714a6e88b30761fae22&_=1536529677993`)
      axios.get(`https://api.openweathermap.org/data/2.5/weather?q=Athens,GR&APPID=dfbbc23cb4274f50546c28374b6ad2ee&units=metric`)
      .then(res => {
        currentWeather = res.data
      })
      axios.get(`https://api.openweathermap.org/data/2.5/forecast?q=Athens,GR&APPID=dfbbc23cb4274f50546c28374b6ad2ee&units=metric`)
      .then(res => {
        forecastWeather = res.data.list
      })
      setTimeout(function(){
        this.setState({ currentWeather,forecastWeather })
        this.getWeatherData()
      }.bind(this),1000)

    
  }

  //when component updates it runs this function
  componentDidUpdate(prevProps,prevState){
    if(prevState.currentWeather && prevState.currentWeather.name && this.props.currentWeather && this.props.currentWeather.name){
      //props.currentWeather.name is the new cityName
      if(prevState.currentWeather.name == this.props.currentWeather.name){
        //if previous city name is same with the new one just render it without update the DOM
      }else{
        //if the previous name is not the same with the new one , it must set the values to variables
        this.setTheState()
      }
    }
  }

  setTheState = () => {
    if(this.props.currentWeather && this.props.forecastWeather){
      this.setState({
        currentWeather: this.props.currentWeather,
        forecastWeather: this.props.forecastWeather
      })
      this.getWeatherData()
    }
  }

  render() {
    const { currentWeather, forecastWeather } = this.state;
    let customTab
    customTab = <CustomTab currentWeather={currentWeather} forecastWeather={forecastWeather}/>
    return (
        <div>{customTab}</div>
      )
  }
}
export default WeatherData


