import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import AppBar from '@material-ui/core/AppBar';
import Typography from '@material-ui/core/Typography';
import Forecast from './forecast.jsx';
import CurrentWeather from './currentWeather';

function TabContainer({ children, dir }) {
  return (
    <Typography component="div" style={{ padding: 8 * 3 }}>
      {children}
    </Typography>
  );
}

TabContainer.propTypes = {
  children: PropTypes.node.isRequired,
};

const styles = {
  bg: {
    // backgroundColor: 'yellow'
  },
  textColor:{
      color: '#007bffe0'
  },
  indicatorColor:{
      backgroundColor: '#007bffe0'
  }
};

const tempreture = {
    display: 'inline-block',
    marginLeft: '1.5em'
}

class CenteredTabs extends React.Component {
    constructor(props){
        super(props)
    }
    state = {
        value: 0,
    };

    handleChange = (event, value) => {
        this.setState({ value });
    };

    render() {
        const { classes, currentWeather, forecastWeather, customStyle } = this.props;
        const { value } = this.state;
        //na valw mellontika na kanei log in kai na krataei se jsno to country kai to username
        //na valw input gia search
        return (
            <div>
                <Paper>
                    <Tabs
                        value={this.state.value}
                        onChange={this.handleChange}
                        //indicatorColor={"#007bffe0"}
                        //textColor="primary"
                        classes={{root: classes.textColor,indicator: classes.indicatorColor}}
                        centered
                    >
                        <Tab label="Today's Weather" />
                        <Tab label="Forecast" />
                    </Tabs>
                </Paper>
                {value === 0 && 
                    <TabContainer >
                        {/* <div className="current-weather" style={customStyle}> */}
                        <CurrentWeather currentWeather={currentWeather}/>
                    </TabContainer>
                }
                {value === 1 &&
                    <TabContainer >
                        <Forecast forecast={forecastWeather} cityName={currentWeather.name}/>
                    </TabContainer>
                }
            </div>
        );
    }
}

CenteredTabs.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(CenteredTabs);



