import React, { Component } from 'react';
import logo from './weathercock.svg';
import './App.css';
import WeatherData from './weatherData'
import 'typeface-roboto'
import SearchCity from './components/searchCity.jsx'
import Error from '@material-ui/icons/Error'

class App extends Component {
  constructor(props){
    super(props)

    this.state = {
        currentWeather: null,
        forecastWeather: null,
        errorMsg: null
    }
  }

  setWeatherVar = (obj) =>{
    this.setState({
      currentWeather: obj.currentWeather,
      forecastWeather: obj.forecastWeather
    })
    // setTimeout(() => {this.setState({currentWeather: obj.currentWeather,forecastWeather: obj.forecastWeather}, function(){
    //       console.log(this.state.currentWeather, 'currentWeather'); 
    //       console.log(this.state.forecastWeather, 'forecastWeather'); 
    // });
    // }, 10)
  }
  showError = (msg) => {
    //console.log(msg)
    this.setState({ errorMsg: msg })
  }

  render() {
    const { currentWeather, forecastWeather, errorMsg } = this.state
    let weatherComp, errorEl
    if(currentWeather && forecastWeather){
      weatherComp = <WeatherData currentWeather={currentWeather} forecastWeather={forecastWeather} />
    }else{
      weatherComp = <WeatherData />
    }
    if(errorMsg != "OK" && errorMsg != null){
      errorEl = <div className="error-msg"><Error color="error"/> {errorMsg} </div>
    }else{
      errorEl = null
    }

    
    return (
      <div className="App">
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <h1 className="App-title">WeatherApp</h1>
          <SearchCity onSet={this.setWeatherVar} error={this.showError}/>
          {errorEl}
        </header>
        {/* <p className="App-intro">
          To get started, edit <code>src/App.js</code> and save to reload.
        </p> */}
        {weatherComp}
        <footer>
          <div>Icons made by <a href="https://www.flaticon.com/authors/vectors-market" title="Vectors Market">Vectors Market</a> from <a href="https://www.flaticon.com/" title="Flaticon">www.flaticon.com</a> is licensed by <a href="http://creativecommons.org/licenses/by/3.0/" title="Creative Commons BY 3.0" target="_blank">CC 3.0 BY</a></div>
        </footer>
      </div>
    );
  }
}

export default App;



