import React, { Component } from 'react';
import { withStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import ArrowDropDown from '@material-ui/icons/ArrowDropDown';
import ArrowDropUp from '@material-ui/icons/ArrowDropUp';
import Moment from 'react-moment';
import moment from 'moment';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import TempColor from './tempColor.jsx'

function CheckArrow(props){
    return ( 
        props.isOpen 
        ? <ArrowDropUp onClick={this.handleClick} style={this.IconStyle}/> 
        : <ArrowDropDown onClick={this.handleClick} style={this.IconStyle}/> 
    )
}

let previousDate = null //to keep track of dates of forecast
let formattedDate = null

function PrintDates(props){
    let myContent = null
    let temp = props.date.main.temp
    let desc = props.date.weather[0].description

    formattedDate = moment(props.date.dt_txt).format('ddd D MMM')

    if(previousDate && previousDate == formattedDate){

    }else{
        myContent = <TableRow className="date-row">
                        <TableCell colSpan={2}><h2>{formattedDate}</h2></TableCell>
                    </TableRow>
        previousDate = formattedDate
    }

    return (
        <React.Fragment>
            {myContent}
            <TableRow key={props.date.dt}>
                <TableCell>
                    <Moment format="HH:mm">{props.date.dt_txt}</Moment>
                </TableCell>
                <TableCell>
                    <TempColor temperature={temp}/>
                    <span>{desc}</span>
                </TableCell>
            </TableRow>
        </React.Fragment>
    )
}

class Forecast extends Component {
    constructor(props){
        super(props)

        this.state = {
            isOpen: false
        }

        this.IconStyle = {
            cursor: "pointer"
        }

    }
    handleClick = () =>{
        this.setState({
            isOpen: !this.state.isOpen
        })
    }

    render() {
        const { isOpen } = this.state
        const { forecast, cityName } = this.props
        return (
            <Grid
                container
                direction="row"
                justify="center"
                alignItems="center"
            >
                <Grid item xs={12}><h1>{cityName}</h1></Grid>
                <Grid item xs={12}>
                    <Table className="table forecast-table">
                        <TableBody>
                            {forecast.map(info => {
                                return(
                                    <PrintDates date={info}/>
                                )
                            })}
                        </TableBody>
                    </Table>
                </Grid>
            </Grid>
        );
    }
}

export default Forecast;



