import React, { Component } from 'react'
import { withStyles } from '@material-ui/core/styles'
import IconButton from '@material-ui/core/IconButton'
import Input from '@material-ui/core/Input'
import InputAdornment from '@material-ui/core/InputAdornment'
import Search from '@material-ui/icons/Search'
import axios from 'axios'


class SearchCity extends Component {
    constructor(props){
        super(props)

        this.state = {
            city: null,
            error: false
        }
    }

    onChange = (e) =>{
        // e = event
        const city = e.target.value
        console.log(city)
        this.setState({ city, error: false })
        // setTimeout(() => {
        //     this.setState({ city, error: false }, function(){
                
        //     })
        // }, 100)
        if (e.key === 'Enter') {
            this.searchCity()
        }
    }

    searchCity = () =>{

        //add to the end of string GR
        let { city } = this.state
        let finalCityName
        city == "Athens" ? finalCityName = city.concat(',GR') : finalCityName = city


        let currentWeather = null
        let forecastWeather = null
        axios.get(`https://api.openweathermap.org/data/2.5/weather?APPID=dfbbc23cb4274f50546c28374b6ad2ee&units=metric`+ '&q='+ finalCityName)
        .then(res => {
            console.log(res)
            currentWeather = res.data
        })
        .catch(error => {
            // handle error
            //console.log(error.response.status);
            if(error.response.status == 404 && error.response.statusText == "Not Found"){
                this.setState({
                    error: true
                })
            }
        })
        setTimeout(() =>{
            if(this.state.error){
                this.props.error("City not found.")
            }else{
                this.props.error('OK')
                //if city is valid
                axios.get(`https://api.openweathermap.org/data/2.5/forecast?APPID=dfbbc23cb4274f50546c28374b6ad2ee&units=metric` + '&q='+ finalCityName)
                .then(res => {
                    forecastWeather = res.data.list
                })
                .catch(error => {
                    // handle error
                    //console.log(error.response.status);
                    // if(error.response.status == 404 && error.response.statusText == "Not Found"){
                    //     this.props.error("City not found.")
                    // }
                })
                //add timeout cause it take a little time to add the data to the variables
                setTimeout(function(){
                    if(currentWeather && forecastWeather){
                        let obj = {
                            currentWeather: currentWeather,
                            forecastWeather: forecastWeather
                        }
                        this.props.onSet(obj)
                    }
                }.bind(this),1000)
            }  
        },100)
        
    }

    render() {
        return (
            <Input onKeyUp={this.onChange} placeholder="Search city" className="city-search-input"
            endAdornment={
                <InputAdornment position="end">
                  <IconButton
                    aria-label="Search city"
                    color="inherit"
                    onClick={this.searchCity}
                  >
                    <Search/>
                  </IconButton>
                </InputAdornment>
            }
            
            />
        )
    }
}

export default SearchCity;



