import React, { Component } from 'react';
//import { Table } from 'reactstrap';
import { withStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';

const styles = theme => ({
    root: {
      width: '100%',
      marginTop: theme.spacing.unit * 3,
      overflowX: 'auto',
    },
    table: {
      minWidth: 700,
    },
  });

class TableComp extends Component {
    render() {
        return (
            <Paper className="root">
                <Table className="table">
                    <TableHead>
                        <TableRow>
                            <TableCell>Time</TableCell>
                            <TableCell numeric>Temp</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                    {this.props.forecast.map(info => {
                        return (
                        <TableRow key={info.dt}>
                            <TableCell>{info.dt_txt}</TableCell>
                            <TableCell numeric>{info.main.temp}C</TableCell>
                        </TableRow>
                        );
                    })}
                    </TableBody>
                </Table>
            </Paper>
        // <div className="table-temp">
        //     <Table hover size="sm" responsive borderless>
        //     <thead>
        //         <tr>
            //         <th>Time</th>
            //         <th>Tempreture</th>
        //         </tr>
        //     </thead>
        //     <tbody>
        //     {this.props.forecast.map(info => {
        //         //console.log(info.dt_txt);
        //         return (
        //         <tr key={info.dt}>
        //             <td>{info.dt_txt}</td>
        //             <td>{info.main.temp}C</td>
        //         </tr>
        //         )
        //     })}
        //     </tbody>
        //     </Table>
        // </div>
        );
    }
}

export default withStyles(styles)(TableComp);



