import React, {Component} from 'react';

class TempColor extends Component{
    constructor(props){
        super(props)
    }

    render(){
        const { customStyle, temperature } = this.props
        let bgClass = []
        if(temperature > 27){
            bgClass.push('bg-red')
        }else if(temperature > 23){
            bgClass.push('bg-orange')
        }else{
            bgClass.push('bg-blue')
        }
        return (
            <span className={[bgClass,'temperature'].join(" ")}>{temperature}&deg;C</span>
        )
    }
}

export default TempColor


